.vacancies{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 46.875em) 41.875em;
    gap: 4em 5em;
    padding-top: 4.5em;
    margin-top: 2em;
    border-top: 1px solid rgba(0, 0, 0, 0.20);
  }
  &__sidebar{
    position: sticky;
    top: 1em;
  }
  &__items{
    @include grid100gap(3.75em);
  }
  &__img{
    width: 100%;
    display: block;
    border-radius: 1em;
  }
  .contacts-form{
    margin-top: 2.5em;
  }
}

.vacancy-card{
  &__title{
    display: flex;
    align-items: center;
    margin-bottom: 1.5em;
    cursor: pointer;
    img{
      width: 1.875em;
      margin-left: 1.125em;
      transition: all .3s;
    }
    span{
      font-size: 1.625em;
      line-height: 115%;
      text-decoration: underline;
    }
  }
  &__desc{
    font-size: 1.125em;
    line-height: 144%;
    color: #444;
  }
  &__nav{
    display: flex;
    margin-top: 1.625em;
  }
  &__btn{
    display: flex;
    align-items: center;
    color: $primary;
    ._view2{
      display: none;
    }
    img{
      width: .875em;
      margin-left: .625em;
      transition: all .3s;
    }
    span{
      font-size: 1.125em;
      line-height: 144%;
    }
  }
  &__bottom{
    display: none;
  }
  &__item{
    color: #444;
    margin-top: 3.25em;
  }
  &._active{
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
    ._toggle img{
      transform: rotate(-180deg);
    }
  }
}


@media screen and (max-width: 992px){
  .vacancies{
    &__grid{
      grid-template-columns: 100%;
      padding-top: 3.125em;
      margin-top: 1.25em;
    }
  }

  .vacancy-card{
    &__title{
      margin-bottom: 1em;
      img{
        width: 1.625em;
      }
      span{
        font-size: 1.375em;
      }
    }
    &__desc{
      font-size: 1em;
      line-height: 150%;
    }
    &__item{
      margin-top: 2.5em;
    }
    &__nav{
      margin-top: 1em;
    }
    &__btn{
      span{
        font-size: 1em;
        line-height: 162%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .vacancies{
    &__img{
      height: 26em;
      object-fit: cover;
    }
  }
}
