.about-quote{
  text-align: center;
  &__top{
    width: 65.625em;
    max-width: 100%;
    margin: 0 auto;
    position: relative;
    padding: 2.75em 4.81em;
    &:before,
    &:after{
      content: '';
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      width: 15.75em;
      height: 0.25em;
      border-radius: 1.875em;
      background: $primary;
    }
    &:before{
      top: 0;
    }
    &:after{
      bottom: 0;
    }
  }
  &__text{
    font-size: 1.875em;
    line-height: 133%;
  }
  &__icon{
    width: 5.375em;
    position: absolute;
  }
  &__icon1{
    left: 0;
    bottom: 0;
  }
  &__icon2{
    top: 0;
    right: 0;
  }
  &__bottom{
    margin-top: 2.5em;
  }
  &__by{
    font-size: 1.375em;
    line-height: 127%;
  }
}

.services{
  &__top{
    max-width: 64em;
    @include grid100gap(2em);
  }
  &__subtitle{
    font-size: 1.5em;
    text-transform: uppercase;
  }
  &__title{
    font-size: 1.875em;
    line-height: 133%;
  }
  &__grid{
    margin-top: 3.75em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 7.25em / 3));
    gap: 5em 3.625em;
  }
}

.service-card{
  &__img{
    @include imgCover(75%);
    border-radius: 1em;
  }
  &__content{
    margin-top: 2em;
    max-width: 23.5em;
    @include grid100gap(1.125em);
  }
  &__title{
    font-size: 1.625em;
    line-height: 123%;
  }
  &__desc{
    color: #444;
    font-size: 1.125em;
    line-height: 133%;
  }
}


@media screen and (max-width: 992px){
  .about-quote{
    margin-top: 3.19em;
    &__top{
      width: 37.5em;
      padding: 2em 0;
      &:before,
      &:after{
        width: 11.25em;
      }
    }
    &__text{
      font-size: 1.25em;
      line-height: 150%;
    }
    &__icon{
      width: 4.25em;
    }
    &__icon1{
      bottom: -3.19em;
    }
    &__icon2{
      top: -3.19em;
    }
    &__bottom{
      margin-top: 2em;
    }
    &__by{
      font-size: 1em;
      line-height: 150%;
    }
  }

  .services{
    &__top{
      gap: 1.5em;
    }
    &__subtitle{
      font-size: 1.25em;
    }
    &__title{
      font-size: 1.375em;
      line-height: 145%;
    }
    &__grid{
      margin-top: 2.75em;
      grid-template-columns: repeat(2,calc(50% - 1.375em));
      gap: 2.75em;
    }
  }

  .service-card{
    &__content{
      margin-top: 1.875em;
      max-width: 20.5em;
    }
    &__title{
      font-size: 1.25em;
      line-height: 130%;
    }
    &__desc{
      font-size: 1em;
      line-height: 125%;
    }
  }
}


@media screen and (max-width: 576px){
  .services{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
