.about-main{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 41.875em - 3.75em) 41.875em;
    gap: 3em 3.75em;
    padding-top: 4.5em;
    margin-top: 2em;
    border-top: 1px solid rgba(0, 0, 0, 0.20);
  }
  .about-info{
    background-color: transparent;
    padding-top: 0;
    padding-right: 0;
    padding-bottom: 0;
    height: unset;
    &:before{
      top: .75em;
    }
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.5em 1.25em;
    margin-top: 3.5em;
  }
  @media screen and (min-width: 993px){
    .about-info{
      .text30{
        letter-spacing: unset;
      }
      &__years{
        margin-bottom: 0;
      }
    }
    .about-item{
      padding: 1.25em 1.25em;
      min-height: 10.75em;
      &__top{
        padding-bottom: .25em;
      }
      &__num{
        font-size: 4.375em;
      }
      &__measure{
        font-size: 1.875em;
      }
      &__bottom{
        width: 12em;
      }
      &__desc{
        font-size: 1.125em;
      }
    }
  }
}

._no-wrap{
  white-space: nowrap;
}

.about-info2{
  height: 100%;
  display: flex;
  flex-direction: column;
  &__title{
    font-size: 1.625em;
    line-height: 134%;
  }
  &__desc{
    margin-top: 1.75em;
    .text20{
      line-height: 150%;
    }
  }
  &__nav{
    display: flex;
    margin-top: auto;
    padding-top: 2em;
  }
}

.about-gallery{
  .main-card{
    height: unset;
    padding-bottom: 46%;
    &__img:after{
      display: none;
    }
    ._view2{
      display: none;
    }
  }
}


@media screen and (max-width: 992px){
  .about-main{
    &__grid{
      grid-template-columns: 100%;
      padding-top: 2.75em;
      margin-top: 1.25em;
    }
    &__items{
      margin-top: 2.5em;
    }
  }

  .about-info2{
    &__title{
      font-size: 1.25em;
      line-height: 150%;
    }
    &__desc{
      margin-top: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .about-main{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .about-gallery{
    .main-card{
      padding-bottom: 146%;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}
