.text{
  @include grid100gap(1.25em);
  li,
  h5,
  p{
    font-size: 1.125em;
    line-height: 144%;
  }
  h5{
     font-weight: 700;
  }
  ol,ul{
    padding-left: .625em;
    @include grid100gap(1.25em);
    li{
      position: relative;
      padding-left: 1em;
    }
  }
  ol {
    counter-reset: list;
    li {
      counter-increment: list;
      &:before {
        content: counter(list) '. ';
      }
    }
  }
  ul{
    li{
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
}



@media screen and (max-width: 992px){
  .text {
    li,
    h5,
    p {
      font-size: 1em;
      line-height: 150%;
    }
  }
}


