.fg {
  position: relative;
  margin: 0;
  input,
  textarea{
    border-radius: 0;
    border: none;
    border-bottom: .07em solid #999;
    padding: 0 .36em;
    height: 3em;
    width: 100%;
    font-size: 1.125em;
    background-color: transparent;
    color: $black;
    font-weight: 300;
    &::placeholder {
      color: #999;
    }
    &:focus {
      border-color: #005EFF;
    }
  }
  textarea{
    padding-top: 1.19em;
    padding-bottom: 1.19em;
    height: 13.0625em;
    resize: vertical;
  }
  &__field{
    position: relative;
  }
  &._error{
    input,
    textarea{
      border-color: $primary;
      color: $primary;
      &::placeholder{
        color: $primary;
      }
    }
    .fg__error-text{
      display: block;
    }
  }
  &__error-text{
    margin-top: .375em;
    color: $primary;
    display: none;
  }
}

.file-input,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 2.625em;
    min-height: 2.0625em;
    color: #777;
    font-weight: 300;
    span{
      font-size: .875em;
      line-height: 155%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: .1875em;
      border: 1.5px solid $primary;
      width: 1.6875em;
      height: 1.6875em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-size: 1.125em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .1875em;
      background-image: url(../img/check.svg);
    }
  }
}

.file-input{
  overflow: hidden;
  input{
    left: unset;
    right: 0;
    height: 30em;
    width: 50em;
    min-width: 100%;
  }
  &__inner{
    @include center;
    padding: 0 1.25em;
    height: 3em;
    border-radius: 0.5em;
    background: #ECECEC;
    color: #666;
    font-weight: 300;
    img{
      width: 1.5em;
      margin-right: .69em;
    }
  }
}


@media screen and (max-width: 992px){
  .fg{
    input,
    textarea{
      font-size: 1.125em;
    }
  }

  .m-check{
    label{
      padding-left: 2.875em;
      span{
        font-size: .75em;
        line-height: 125%;
      }
      &:before{
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }
}


