.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .5;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: $white;
        width: 30em;
        max-width: 100%;
        position: relative;
        z-index: 3;
        padding: 2.5em 1.875em 2.875em;
        border-radius: 0;
        display: inline-block;
    }
    .close-btn{
        position: absolute;
        top: 1.25em;
        right: 1em;
    }
}

.close-btn{
    width: 1.25em;
    @include center;
    img{
        width: 100%;
    }
}

.modal-form{
    margin-top: 1.5em;
    &__fields{
        @include grid100gap(1.5em);
    }
    .m-check{
        margin-top: 1.125em;
        label{
            cursor: default;
        }
        input{
            width: 1.6875em;
            height: 1.6875em;
            top: .1875em;
        }
        a{
            text-decoration: underline;
        }
        b{
            font-weight: 300;
            text-decoration: underline;
        }
    }
    &__btn{
        margin-top: 2em;
        height: 3.5em;
        padding: 0 1.375em;
    }
}

._open-vertical-video{
    .fancybox__content{
        width: auto !important;
        height: auto !important;
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__dialog {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: $vh;
            overflow-y: auto;
        }
    }

    .modal-form{
        &__btn{
            height: 4.125em;
            span{
                font-size: 1.125em;
            }
        }
        .m-check{
            input{
                top: 50%;
                transform: translateY(-50%);
            }
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {

        }
    }
}
