.contacts{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 41.875em - 3.75em) 41.875em;
    gap: 2em 3.75em;
    padding-top: 4.5em;
    margin-top: 2em;
    border-top: 1px solid rgba(0, 0, 0, 0.20);
  }
  &__col3{
    grid-column: 1/-1;
  }
}

.contacts-info{
  @include grid100gap(1.75em);
  position: relative;
  padding-left: 3em;
  &:before{
    content: '';
    position: absolute;
    width: 0.5625em;
    height: 4.6875em;
    top: .5em;
    left: 0;
    background-color: $primary;
  }
  &__item{
    font-size: 1.125em;
    line-height: 155%;
  }
}

.contacts-form{
  max-width: 36.25em;
  &__title{
    font-size: 1.625em;
    line-height: 120%;
  }
  &__grid{
    margin-top: 1em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.875em));
    gap: 1.25em 3.75em;
  }
  .m-check{
    margin-top: 2em;
    max-width: 28.25em;
  }
  &__nav{
    display: flex;
    margin-top: 2em;
  }
}

._full{
  grid-column: 1/-1;
}

.contacts-address{
  &__title{
    font-size: 1.5em;
  }
  .map{
    margin-top: 1.5em;
  }
}

.map{
  height: 40em;
  border-radius: 1.25em;
  overflow: hidden;
}

.error404{
  &__inner{
    margin-top: 2em;
    border-top: 1px solid rgba(0, 0, 0, 0.20);
  }
  &__content{
    width: 26em;
    margin: 3em auto 0;
    max-width: 100%;
    text-align: center;
    @include grid100gap(1.75em);
  }
  &__title{
    font-size: 1.875em;
    letter-spacing: .05em;
  }
  &__num{
    font-size: 12.5em;
    line-height: 70%;
    letter-spacing: .05em;
    color: $primary;
  }
  &__desc{
    font-size: 1.25em;
    line-height: 150%;
  }
  &__nav{
    display: flex;
    justify-content: center;
  }
}


@media screen and (max-width: 992px){
  .contacts{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 3.75em 1.5em;
      padding-top: 2.25em;
      margin-top: 1.25em;
    }
  }

  .contacts-info{
    gap: 1.25em;
    padding-left: 1.25em;
    &:before{
      width: 0.4375em;
      height: 5.4375em;
    }
    &__item{
      font-size: 1em;
      line-height: 150%;
    }
  }

  .contacts-form{
    &__title{
      font-size: 1.375em;
      letter-spacing: .03em;
    }
    &__grid{
      margin-top: .75em;
      grid-template-columns: 100%;
      gap: 1em;
    }
  }

  .contacts-address{
    &__title{
      font-size: 1.25em;
    }
    .map{
      margin-top: 1.25em;
    }
  }

  .error404{
    &__inner{
      margin-top: 1.25em;
    }
    &__content{
      width: 20em;
      margin-top: 2.75em;
      gap: 1.125em;
    }
    &__title{
      font-size: 1.5em;
    }
    &__num{
      font-size: 9.75em;
    }
    &__desc{
      font-size: 1em;
    }
  }

}


@media screen and (max-width: 576px){
  .contacts{
    &__grid{
      grid-template-columns: 100%;
    }
    &__col2{
      order: 3;
    }
  }
}
